import React, { useState } from "react";
import "./styles.css";

import faker from "faker";

faker.seed(123);

const data = [...Array(50)].map((item) => ({
  id: faker.random.uuid(),
  name: faker.commerce.productName(),
  image: faker.random.image(),
  price: faker.commerce.price(),
  material: faker.commerce.productMaterial(),
  brand: faker.lorem.word(),
  inStock: faker.random.boolean(),
  fastDelivery: faker.random.boolean(),
  ratings: faker.random.arrayElement([1, 2, 3, 4, 5]),
  offer: faker.random.arrayElement([
    "Save 50",
    "70% bonanza",
    "Republic Day Sale"
  ]),
  idealFor: faker.random.arrayElement([
    "Men",
    "Women",
    "Girl",
    "Boy",
    "Senior"
  ]),
  level: faker.random.arrayElement([
    "beginner",
    "amateur",
    "intermediate",
    "advanced",
    "professional"
  ]),
  color: faker.commerce.color()
}));

const getMinMaxPrice = (products) => {
  const prices = products.map((item) => item.price);
  return { minPrice: Math.min(...prices), maxPrice: Math.max(...prices) };
};

const filterByStock = (products, stockChecked) => {
  return stockChecked ? products.filter(({ inStock }) => inStock) : products;
};

const filterByFastDelivery = (products, infastDeliveryChecked) => {
  return infastDeliveryChecked
    ? products.filter(({ fastDelivery }) => fastDelivery)
    : products;
};

const filterByPriceRange = (products, priceSlider) => {
  return products.filter(({ price }) => price <= Number(priceSlider));
};

const filterbySort = (products, sortby) => {
  if (sortby === "low-to-high") {
    return products.sort((a, b) => a.price - b.price);
  } else if (sortby === "high-to-low") {
    return products.sort((a, b) => b.price - a.price);
  } else {
    return products;
  }
};

export default function App() {
  // let data = [];
  const [products, setProducts] = useState(data);
  const [instockChecked, setInstockChecked] = useState(false);
  const [infastDeliveryChecked, setInfastdeliveryChecked] = useState(false);
  const [sortby, setSortBy] = useState(null);
  const { minPrice, maxPrice } = getMinMaxPrice(products);
  const [priceSlider, setPriceSlider] = useState(maxPrice);

  //stock filter should be base filter

  const filteredInStock = filterByStock(products, instockChecked);

  //adding in previous result ðŸ‘† // stockChecked is same checked attribute

  const filteredInFastDelivery = filterByFastDelivery(
    filteredInStock,
    infastDeliveryChecked
  );

  const filteredInPriceRange = filterByPriceRange(
    filteredInFastDelivery,
    priceSlider
  );

  const filteredProducts = filterbySort(filteredInPriceRange, sortby);

  const clearFilters = () => {
    setInstockChecked(false);
    setInfastdeliveryChecked(false);
    setPriceSlider(maxPrice);
    setSortBy(null);
  };

  return (
    <>
      <div className="App" style={{ display: "flex", flexWrap: "wrap" }}>
        <div className="filters">
          <h2>FILTER</h2>
          <input
            type="radio"
            value="high-to-low"
            name="sort-price"
            onChange={(e) => {
              setSortBy(e.target.value); //console.log this, it willreturn high-to low    ===>  console.log(e.target.value)
            }}
            checked={sortby === "high-to-low"} //checked is property in input, it returns true/false
          />
          <label htmlFor="high-to-low">High to Low</label>

          <input
            type="radio"
            value="low-to-high"
            name="sort-price"
            onChange={(e) => {
              setSortBy(e.target.value);
            }}
            checked={sortby === "low-to-high"}
          />
          <label htmlFor="low-to-high">Low to High</label>

          <label className="container">
            In Stock
            <input
              onChange={(e) => {
                setInstockChecked(e.target.checked);
              }}
              type="checkbox"
              checked={instockChecked}
            />
            <span className="checkmark"></span>
          </label>

          <label className="container">
            Fast Delivery
            <input
              onChange={(e) => {
                setInfastdeliveryChecked(e.target.checked);
              }}
              type="checkbox"
              checked={infastDeliveryChecked}
            />
            <span className="checkmark"></span>
          </label>

          <input
            type="range"
            onInput={(e) => {
              setPriceSlider(e.target.value);
            }}
            min={minPrice}
            max={maxPrice}
            value={priceSlider}
          ></input>
          <button onClick={clearFilters}>CLEAR</button>
        </div>
        {filteredProducts.map(
          ({
            id,
            name,
            image,
            price,
            productName,
            inStock,
            level,
            fastDelivery
          }) => (
            <div
              key={id}
              style={{
                border: "1px solid #4B5563",
                borderRadius: "0 0 0.5rem 0.5rem",
                margin: "1rem",
                maxWidth: "10%",
                padding: "0 0 1rem"
              }}
            >
              <img src={image} width="100%" height="auto" alt={productName} />
              <h3> {name} </h3>
              <div>Rs. {price}</div>
              {inStock && <div> In Stock </div>}
              {!inStock && <div> Out of Stock </div>}
              <div>{level}</div>
              {fastDelivery ? (
                <div> Fast Delivery </div>
              ) : (
                <div> 3 days minimum </div>
              )}
            </div>
          )
        )}
      </div>
    </>
  );
}
